.radial-wrapper::after {
/*  background: #fff;   */
  content: "";
  background: rgb(0, 0, 0);
  border-radius: 50%;
  width: 156px;
  height: 156px;
  position: absolute;
  top: 12px;
  left: 12px;
}

.radial-progress-wrapper .wedge1, .radial-progress-wrapper .marker1 {
  background: #A4BA32;
}

.radial-progress-wrapper .radial-left-section .wedge1 {
  -webkit-transform: rotateZ(-45deg);
          transform: rotateZ(-45deg);
}
